DefaultCyProperty.cy = () => {
  cy.get("@wrappedLabel").then((wrappedLabel) =>
    cy.dataCy("button").should("contain", wrappedLabel)
  );
  cy.wrap("This will be reset").as("wrappedLabel");
};
